  margin: 0.5rem 1rem 0 0.5rem;
  font-family: "Cardo", serif;
  letter-spacing: 0.1rem;
  color: white;
  cursor: pointer;
}

.scroll {
  position: fixed;
  left: 50vw;
  bottom: 2vh;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  font-size: 2rem;
  color: white;
  z-index: 2;
